import React from 'react';

/**/

export const ordercheck = (datas) =>{
   let count = 0;
   let date = null;
  if(datas){
    datas.forEach(data => {
        if(data.status === 0 || data.status === 1){
            count += 1;
            date = data.shopping_date;
        }
    });
    return {
        count:  count + '',
        date: date
    }
  }
  else{
    return '';
  }
}
/*   */

export const orderHistoryStatus = (status) =>{
  switch (status) {
    case 0:
      return <div className="bg-info text-white"></div>
    case 1:
      return <div className="bg-info text-white"></div>
    case 2:
      return <div className="font-weight-bold"></div>
    default:
      break;
  }
}
